@font-face {
	font-family: 'Inter';
	font-weight: 400;
	font-display: swap;
	src: url("../fonts/Inter-Regular.woff2") format("woff2"),
			 url("../fonts/Inter-Regular.woff") format("woff"),
			 url("../fonts/Inter-Regular.ttf") format("ttf");
}
